import React from 'react';
import {CardNumberElement, CardExpiryElement, CardCvcElement, injectStripe} from 'react-stripe-elements';
import {
    Button, Grid
} from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
const createOptions = () => {
    return {
      style: {
        base: {
          fontSize: '16px',
          margin:"5px",
          color: '#424770',
          fontFamily: 'Open Sans, sans-serif',
          letterSpacing: '0.025em',
          '::placeholder': {
            color: '#aab7c4',
          },
        },
        invalid: {
          color: '#c23d4b',
        },
      }
    }
};

class BillingForm extends React.Component{
    
    state = {
        value: {
            cardHolderName: "",
            cardNumber: "",
            cardExpiry: "",
            cardCvc: ""
        },
        errorMessageObj: {
            cardNumber: "",
            cardExpiry: "",
            cardCvc: ""
        },
        isError: false,
        errorMessage: ""
    };

    handleChange = (data) => {
        let { errorMessageObj } = this.state;
        if(data.error){
            errorMessageObj[data.elementType] = data.error.message;
            this.setState({ errorMessageObj })
        }
        // if (error) {
        //   this.setState({errorMessage: error.message});
        // }
    };

    handleChangeName = (name) => {
        let { value } = this.state;
        value.cardHolderName = name;

        this.setState({
            value
        })
    }

    handleSubmit = (evt) => {
      evt.preventDefault();

      if (this.props.stripe) {
        this.props.showLoader();
        this.props.stripe.createToken().then((res)=>{
            if(res.error){
                this.setState({
                    isError:true,
                    errorMessage: res.error.message
                }, () => {
                    this.props.hideLoader();
                })
            }else{
                this.setState({
                    isError:false,
                    errorMessage: ''
                },()=>{
                    this.props.handleTokenCreation(res);
                })
                
            }
            // this.props.handleTokenCreation
        });
      }

        // this.props.purchaseSubscriptionButton();
    };

    render(){
        return(
            <form onSubmit={this.handleSubmit.bind(this)}>
                <Grid container spacing={0}>
                    {
                        this.state.isError
                        ?(
                            <Grid item xs={12}>
                            <Alert severity="error">{ this.state.errorMessage }</Alert>
                            </Grid>
                        )
                        :<></>
                    }
                    <Grid item xs={12}>
                        <div className="formGroupContainer">
                            <label className="formLabel">Name On Credit Card</label>
                            <input className="cardHolderName" placeholder="Card Holder Name" onChange={(event) => this.handleChangeName(event.target.value, "", "name")} />
                        </div>
                    </Grid>
                    <Grid item xs={12}>
                        <div className="formGroupContainer">
                            <label className="formLabel">Card number</label>
                            <CardNumberElement
                                variant="outlined"
                                hidePostalCode={true}
                                className="cardHolderName"
                                onChange={this.handleChange}
                                {...createOptions()}
                            />
                            {/* <div className="stripe-error" role="alert">
                            {this.state.errorMessage.cardNumber}
                            </div> */}
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="formGroupContainer">
                            <label className="formLabel">Expiration date</label>
                            <CardExpiryElement
                                variant="outlined"
                                // hidePostalCode={true}
                                className="cardHolderName"
                                onChange={this.handleChange}
                                {...createOptions()}
                            />
                            {/* <div className="stripe-error" role="alert">
                            {this.state.errorMessage.expirationDate}
                            </div> */}
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="formGroupContainer">
                            <label className="formLabel">CVC</label>
                            <CardCvcElement
                                variant="outlined"
                                // hidePostalCode={true}
                                className="cardHolderName"
                                onChange={this.handleChange}
                                {...createOptions()}
                            />
                            {/* <div className="stripe-error" role="alert">
                            {this.state.errorMessage.CVC}
                            </div> */}
                        </div>
                    </Grid>
                </Grid>

                <div className="formGroupContainer">
                <Button type="submit" variant="contained" color="primary" className="pay-button">Purchase Subscription</Button>
                </div>
            </form>
        )
    }

}

export default injectStripe(BillingForm);

