import React from "react";
import CropOriginal from '@material-ui/icons/CropOriginal';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import CloseIcon from "@material-ui/icons/Close";
import DeleteOutlined from "@material-ui/icons/DeleteOutlined";
import Visibility from "@material-ui/icons/Visibility";
import {IconButton} from "@material-ui/core";
import './Document.css';

export default class SingleFile extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      hover: false
    };
  }

  onMouseOver = () => {
    this.setState({hover: true});
  };

  onMouseOut = () => {
    this.setState({hover: false});
  };

  render() {
    const {file, userId} = this.props;
    if(file.id) {
      file.fileId = file.id;
    }

    return (
      <div className={'file-tile'} onMouseEnter={() => this.onMouseOver()} onMouseLeave={() => this.onMouseOut()}>
        <div>
          {file.type === '.pdf' ? <PictureAsPdfIcon style={{fontSize: 60}}/> :
            <CropOriginal style={{fontSize: 60}}/>}
          <div className={'file-name'}>{file.name}</div>
        </div>
        {this.state.hover && <div className={'file-overlay'}>
          <IconButton aria-label="close" onClick={() => this.props.handlePreview(file.fileId)} className={'document-icon-button'}>
            <Visibility />
          </IconButton>
          {
            userId === file.createdBy &&
            (<IconButton aria-label="close" onClick={() => this.props.handleRemove(file.fileId)}
                        className={'document-icon-button'}>
              <DeleteOutlined/>
            </IconButton>)
          }
        </div>}
    </div>
    )
  }
}
