import React from 'react';

import {Redirect} from "react-router-dom";
import {withStyles} from '@material-ui/core/styles';
import {connect} from 'react-redux';

import {Button, Grid, Paper} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import CountriesAndTimezones from 'countries-and-timezones'
import {TextValidator, ValidatorForm} from 'react-material-ui-form-validator';
import {getUserPracticeRequest,} from '../../../User/UserApiActions';

import {
  getCoordinatesRequest,
  getPracticeDetailsRequest,
  setupPracticeRequest,
  updatePracticeRequest
} from '../../PracticeApiActions';

import MobileNumber from "../../../../utils/components/MobileNumber/MobileNumber";
import ConstantMessages from '../../../../utils/Strings';
import MessageModal from "../../../../utils/components/MessageModal/MessageModal";
import Constants from '../../../../utils/Constants';

import {ONLY_BLANK_SPACES, ONLY_CHARACTER_ACCEPT} from '../../../../utils/Constants';
import UtilHelper from '../../../../utils/UtilHelper';

import {showSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    // textAlign: 'center',
    color: theme.palette.text.secondary,
  }
});

class SetupPracticeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      practice: {
        name: '',
        email: '',
        phone: '',
        addr1: '',
        addr2: '',
        addr3: '',
        city: '',
        state: '',
        pincode: '',
        country: '',
        timezone: '',
        currency: '',
        practiceAdminDisplayName: '',
        practiceAdminEmail: '',
        practiceAdminPhone: ''
      },
      isUpdatePractice: false,
      validPhoneNumber: false,
      validPracticeAdminPhoneNumber: false,
      timezoneList: []

    }
  }

  componentDidMount = () => {
    ValidatorForm.addValidationRule(ONLY_CHARACTER_ACCEPT, UtilHelper.validateString);
    ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
  };

  componentWillUnmount() {
    ValidatorForm.removeValidationRule(ONLY_CHARACTER_ACCEPT);
    ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
  }

  handleChange = (event) => {
    const practice = Object.assign({}, this.state.practice);
    practice[event.target.name] = event.target.value;
    this.setState({practice});
  };

  handleChangeAutocomplete = (elementName, value) => {
    if(elementName === "country"){
      const practice = Object.assign({}, this.state.practice);
      const selectedCountry = Constants.COUNTRY_LIST.find((country) => country.country === value);
      practice[elementName] = value;
      practice['currency'] = selectedCountry.currency;
      const timezoneList = CountriesAndTimezones.getCountry(selectedCountry.countryCode);
      this.setState({practice, timezoneList: timezoneList.timezones});
    }else{
      const practice = Object.assign({}, this.state.practice);
      practice[elementName] = value;
      this.setState({practice});
    }
    
  };

  handleSubmit = () => {
    this.setState({formSubmit: true});
    if (this.state.validPhoneNumber && this.state.validPracticeAdminPhoneNumber) {
      const {practice} = this.props;
      if (this.props.isUpdatePractice) {
        this.props.updatePractice(practice.id, this.state.practice);
      } else {
        this.props.setupPractice(this.state.practice);
      }
    }
  };

  updateMobileNumber = (phone, validPhoneNumber) => {
    const practice = Object.assign({}, this.state.practice);
    practice['phone'] = phone;
    this.setState({practice, validPhoneNumber});
  };

  updatePracticeAdminMobileNumber = (practiceAdminPhone, validPracticeAdminPhoneNumber) => {
    const practice = Object.assign({}, this.state.practice);
    practice['practiceAdminPhone'] = practiceAdminPhone;
    this.setState({practice, validPracticeAdminPhoneNumber});
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.practice !== this.props.practice) {
      if (!nextProps.practice.phone || nextProps.practice.phone === "") {
        this.setState({validPhoneNumber: false});
      } else {
        this.setState({validPhoneNumber: true});
      }

      if (!nextProps.practice.practiceAdminPhone || nextProps.practice.practiceAdminPhone === "") {
        this.setState({validPracticeAdminPhoneNumber: false});
      } else {
        this.setState({validPracticeAdminPhoneNumber: true});
      }

      let timezoneList = [];
      if(nextProps.practice.country){
        const selectedCountry = Constants.COUNTRY_LIST.find((country) => country.country === nextProps.practice.country);
        timezoneList = CountriesAndTimezones.getCountry(selectedCountry.countryCode);
      }
      
      this.setState({practice: Object.assign({}, nextProps.practice), timezoneList: timezoneList.timezones});
    }
  }

  render() {
    const {practice, isUpdatePractice} = this.state;
    return (
      <ValidatorForm
        ref="form"
        onSubmit={this.handleSubmit}
        noValidate
        onError={errors => console.log(errors)}
      >
        <Grid container spacing={3}>
          <Grid item xs={12} className="informationHeader practiceOwnerInformation">
            Practice Information
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              variant="outlined"
              required
              type="text"
              label="Practice Name" onChange={this.handleChange}
              name="name" value={practice.name || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextValidator
              variant="outlined"
              fullWidth
              required
              label="Practice Email" onChange={this.handleChange}
              name="email" value={practice.email || ''} validators={['required', 'isEmail']}
              errorMessages={['this field is required', 'email is not valid']}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <MobileNumber phone={practice.phone ? practice.phone : ""}
                          updateMobileNumber={this.updateMobileNumber}/>
            {
              this.state.formSubmit && !this.state.validPhoneNumber ?
                <div className="phone-error">Please enter valid mobile number</div> : ''
            }
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              variant="outlined"
              required
              label="Address 1" onChange={this.handleChange}
              name="addr1" value={practice.addr1 || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_AREA]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_area]}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              variant="outlined"
              required
              label="Address 2" onChange={this.handleChange}
              name="addr2" value={practice.addr2 || ''} 
              validators={['required',ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_AREA]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              required
              variant="outlined"
              label="Address 3" onChange={this.handleChange}
              name="addr3" value={practice.addr3 || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_AREA]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field]}
            />
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              variant="outlined"
              required
              label="City" onChange={this.handleChange}
              name="city" value={practice.city || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD, ONLY_CHARACTER_ACCEPT]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field, ConstantMessages.digit_not_allowed]}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              variant="outlined"
              required
              label="State" onChange={this.handleChange}
              name="state" value={practice.state || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD, ONLY_CHARACTER_ACCEPT]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field, ConstantMessages.digit_not_allowed]}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              required
              variant="outlined"
              label="Zip/Pin Code" onChange={this.handleChange}
              name="pincode" value={practice.pincode || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'isNumber', 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_ZIP_CODE, 'minStringLength:' + Constants.VALIDATIONS.MINIMUM_CHARACTERS_ZIP_CODE]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, Constants.ERROR_MESSAGES.only_number_accept, ConstantMessages.maximum_allowed_characters_for_zip_code, ConstantMessages.minimum_allowed_characters_for_zip_code]}
            />
          </Grid>

          <Grid item xs={12} sm={4}>
            <Autocomplete
              // multiple
              id="size-small-outlined-multi"
              size="small"
              name="country"
              // getOptionLabel={(option) => option.country}
              options={Constants.COUNTRY_LIST.map((country) => country.country)}
              onChange={(e, value) => this.handleChangeAutocomplete("country", value)}
              disableClearable
              value={practice.country ||  null}
              renderInput={(params) => {
                if(practice.country){
                  return (
                    <TextValidator name="country" {...params} fullWidth required variant="outlined" 
                      label="Country" placeholder="Country" 
                    />
                  )
                }else{
                  return (
                    <TextValidator name="timezone" {...params} fullWidth required variant="outlined" 
                      label="Country" placeholder="Country" 
                      validators={['required']}
                      errorMessages={['this field is required']}
                    />
                  )
                }
              }}
            />
            {/* <TextValidator
              fullWidth
              required
              variant="outlined"
              label="Country" onChange={this.handleChange}
              name="country" value={practice.country || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD, ONLY_CHARACTER_ACCEPT]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field, ConstantMessages.digit_not_allowed]}
            /> */}
          </Grid>
          <Grid item xs={12} sm={4}>
            <Autocomplete
              // multiple
              id="size-small-outlined-multi"
              size="small"
              name="timezone"
              options={this.state.timezoneList || []}
              onChange={(e, value) => this.handleChangeAutocomplete("timezone", value)}
              disableClearable
              value={practice.timezone || null}
              renderInput={(params) => {
                if(practice.timezone){
                  return (
                    <TextValidator name="timezone" {...params} fullWidth required variant="outlined" 
                      label="Timezone" placeholder="Timezone" 
                    />
                  )
                }else{
                  return (
                    <TextValidator name="timezone" {...params} fullWidth required variant="outlined" 
                      label="Timezone" placeholder="Timezone" 
                      validators={['required']}
                      errorMessages={['this field is required']}
                    />
                  )
                }
              }}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <Autocomplete
              // multiple
              id="size-small-outlined-multi"
              size="small"
              name="currency"
              options={Constants.CURRENCY_LIST}
              onChange={(e, value) => this.handleChangeAutocomplete("currency", value)}
              disableClearable
              value={practice.currency || null}
              renderInput={(params) => {
                if(practice.currency){
                  return (
                    <TextValidator name="currency" {...params} fullWidth required variant="outlined" 
                      label="Currency" placeholder="Currency" 
                    />
                  )
                }else{
                  return (
                    <TextValidator name="currency" {...params} fullWidth required variant="outlined" 
                      label="Currency" placeholder="Currency" 
                      validators={['required']}
                      errorMessages={['this field is required']}
                    />
                  )
                }
              }}
            />
          </Grid>


          <Grid item xs={12} className="informationHeader practiceOwnerInformation">
            Practice Admin Information
          </Grid>

          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              required
              variant="outlined"
              label="Name" onChange={this.handleChange}
              name="practiceAdminDisplayName" value={practice.practiceAdminDisplayName || ''} 
              validators={['required', ONLY_BLANK_SPACES, 'maxStringLength:' + Constants.VALIDATIONS.MAXIMUM_CHARACTERS_TEXT_FIELD, ONLY_CHARACTER_ACCEPT]}
              errorMessages={['this field is required', Constants.ERROR_MESSAGES.blank_spaces_not_allowed, ConstantMessages.maximum_allowed_characters_for_text_field, ConstantMessages.digit_not_allowed]}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <TextValidator
              fullWidth
              variant="outlined"
              required
              disabled={true}
              label="Email" onChange={this.handleChange}
              name="practiceAdminEmail" value={practice.practiceAdminEmail || ''} validators={['required', 'isEmail']}
              errorMessages={['this field is required', 'email is not valid']}
            />
          </Grid>
          <Grid item xs={12} sm={4}>
            <MobileNumber phone={practice.practiceAdminPhone ? practice.practiceAdminPhone : ""}
                          updateMobileNumber={this.updatePracticeAdminMobileNumber}/>
            {
              this.state.formSubmit && !this.state.validPracticeAdminPhoneNumber ?
                <div className="phone-error">Please enter valid mobile number</div> : ''
            }
          </Grid>
          <Grid item xs={12}>
            <Button variant="contained" color="primary" className="setupButton" type="submit">
              {
                isUpdatePractice
                  ? "Save"
                  : "Setup"
              }
            </Button>
          </Grid>
        </Grid>


      </ValidatorForm>
    )
  }
}

/**
 * Component to add/update prectice information
 */
class SetupPracticeStepOne extends React.Component {
  _isMounted = false;

  constructor(props) {
    super(props);

    this.state = {
      practice: {},
      isUpdatePractice: false
    }
  }

  /**
   * Method to update user details
   */
  handleUpdateUser = (userId, data) => {
    this.props.updateUserRequest(userId, data).then(() => {
      this.refs.messageModal.setState({
        showModal: true,
        title: ConstantMessages.updateUserNotificationTitle,
        message: ConstantMessages.updateUserSuccessMessage,
        showOkButton: true,
        handleOk: () => {
        },
        okText: 'Ok',
        cancelText: 'Cancel'
      });
    }, (error) => {
    });
  };

  setupPractice = (data) => {
    if (data) {
      this.props.getCoordinates(data.pincode).then((res) => {
        if (res && res.length > 0) {
          
          data.coordinates = [res[0].geometry.location.lat, res[0].geometry.location.lng];

          this.props.setupPractice(data).then((res) => {
            if (res) {
              this.getUserPractice();
              this.props.nextButtonClick();
            }
          }, (err)=>{
          })

        }else{
          this.props.showSnackbar(ConstantMessages.incorrectPincode, Constants.SNACKBAR_VARIAINT.ERROR);
        }
      })
    }
  };

  updatePractice = (practiceId, data) => {
    this.props.updatePractice(practiceId, data);
    if(this.props.nextButtonClick){
      this.props.nextButtonClick();
    }else{}
    this.getUserPractice();
  };

  componentDidMount() {
    this._isMounted = true;
    if (this.props.loggedInUser) {
      const {id: userId} = this.props.loggedInUser;
      this.props.getUserRequest(userId).then((response) => {
        if (response.PracticeId) {
          response.Practice.practiceAdminDisplayName = response.displayName;
          response.Practice.practiceAdminEmail = response.email;
          response.Practice.practiceAdminPhone = response.phone ? response.phone : "";
          if (this._isMounted) {
            this.setState({practice: response.Practice, isUpdatePractice: true});
          }
        } else {
          if (this._isMounted) {
            response.practiceAdminDisplayName = response.displayName;
            response.practiceAdminEmail = response.email;
            this.setState({practice: response, isUpdatePractice: false});
          }
        }
        

      }, (error) => {
      });
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  getUserPractice = () => {
    if (this.props.loggedInUser && !this.props.loggedInUserPractice) {
      const {id: userId} = this.props.loggedInUser;
      this.props.getUserRequest(userId).then((res) => {
        if (res) {
          this.props.nextButtonClick();
        }
      });
    }
  };

  render() {
    const {currentRole, classes} = this.props;
    if (currentRole && !currentRole.isPracticeAdmin)
      return <Redirect to="/"/>;
    else
      return (
        <>
          <div className={classes.root}>
            <Grid container>
              <Grid item xs={12}>
                <Paper className={`${classes.paper} removePaperShadow`}>
                  <SetupPracticeForm
                    classes
                    isUpdatePractice={this.state.isUpdatePractice}
                    practice={this.state.practice}
                    handleUpdateUser={this.handleUpdateUser}
                    setupPractice={(data) => this.setupPractice(data)}
                    updatePractice={(practiceId, data) => this.updatePractice(practiceId, data)}
                    currentRole={this.props.currentRole}/>
                </Paper>
              </Grid>
            </Grid>
          </div>

          <MessageModal
            ref="messageModal"
          />
        </>
      )
  }

}

const mapStateToProps = (state) => {
  return {
    loggedInUser: state.auth.loggedInUser,
    currentRole: state.auth.currentRole,
    loggedInUserPractice: state.auth.loggedInUserPractice
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    getUserRequest: (id) => {
      return dispatch(getUserPracticeRequest(id)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    getCoordinates: (pincode) => {
      return dispatch(getCoordinatesRequest(pincode)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    setupPractice: (data) => {
      return dispatch(setupPracticeRequest(data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    getPracticeDetails: (practiceId) => {
      return dispatch(getPracticeDetailsRequest(practiceId)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },

    updatePractice: (practiceId, data) => {
      return dispatch(updatePracticeRequest(practiceId, data)).then((res) => {
        return Promise.resolve(res);
      }, (error) => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

SetupPracticeStepOne = connect(
  mapStateToProps,
  mapDispatchToProps
)(SetupPracticeStepOne);

export default withStyles(styles)(SetupPracticeStepOne);
