import React, {Component} from 'react';
import {Button, Grid} from "@material-ui/core";
import {TextValidator, ValidatorForm} from "react-material-ui-form-validator";
import {connect} from "react-redux";
import {updateConsultNote, getConsultNotes} from "../../ConsultApiActions";
import Constants, {ONLY_BLANK_SPACES} from "../../../../utils/Constants";
import UtilHelper from "../../../../utils/UtilHelper";
import './SoapForm.css';

import ConstantMessages from '../../../../utils/Strings';
import {showSnackbar} from "../../../../utils/components/AppLoader/AppLoaderActions";

class SoapForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      soap: {
        subjective: "",
        objective: "",
        assessment: "",
        plan: "",
      },
      showError: false
    };
  }

  componentDidMount = () => {
    this.getSoapNote();
    ValidatorForm.addValidationRule(ONLY_BLANK_SPACES, UtilHelper.validateBlankSpaces);
  };

  componentWillUnmount() {
    ValidatorForm.removeValidationRule(ONLY_BLANK_SPACES);
  }

  getSoapNote = () => {
    this.props.getConsultNotes(this.props.consultId)
      .then(res => {
        if(res.data) {
          const soap = {
            subjective: res.data.subjective,
            objective: res.data.objective,
            assessment: res.data.assessment,
            plan: res.data.plan,
          };
          this.setState({soap});
        }
      }, error => {
        this.props.showSnackbar(ConstantMessages.unableGetSOAPNote, Constants.SNACKBAR_VARIAINT.ERROR);
      });
  };

  handleChange = (event) => {
    const soap = Object.assign({}, this.state.soap);
    soap[event.target.name] = event.target.value;
    this.setState({soap, showError: false});
  };

  handleSubmit = e => {
    const formData = {...this.state.soap, consultId: this.props.consultId};
    const soap = this.state.soap;
    if((soap.subjective === null || soap.subjective === '') &&
      (soap.objective === null || soap.objective === '') &&
      (soap.assessment === null || soap.assessment === '') &&
      (soap.plan === null || soap.plan === '')) {
      this.setState({showError: true});
    } else {
      this.setState({showError: false}, () => {
        this.props.updateConsultNote(formData).then(res => {
          this.props.showSnackbar(ConstantMessages.SOAPNoteSuccessUpdate, Constants.SNACKBAR_VARIAINT.SUCCESS);
        }, error => {
          this.props.showSnackbar(ConstantMessages.unableToUpdateSOAPNote, Constants.SNACKBAR_VARIAINT.ERROR);
        });
      });
    }
  };

  render() {
    const {soap} = this.state;

    return (
      <ValidatorForm
        ref="form"
        onSubmit={this.handleSubmit}
        noValidate
        onError={errors => console.log(errors)}
      >
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <TextValidator
              fullWidth
              multiline
              rows="2"
              variant="outlined"
              rowsMax="4"
              label="Subjective" onChange={this.handleChange}
              name="subjective" value={soap.subjective}
              validators={[ONLY_BLANK_SPACES]}
              errorMessages={[Constants.ERROR_MESSAGES.blank_spaces_not_allowed]}
            />
          </Grid>
          <Grid item xs={12}>
            <TextValidator
              fullWidth
              multiline
              rows="2"
              variant="outlined"
              rowsMax="4"
              label="Objective" onChange={this.handleChange}
              name="objective" value={soap.objective}
              validators={[ONLY_BLANK_SPACES]}
              errorMessages={[Constants.ERROR_MESSAGES.blank_spaces_not_allowed]}
            />
          </Grid>
          <Grid item xs={12}>
            <TextValidator
              fullWidth
              multiline
              rows="2"
              variant="outlined"
              rowsMax="4"
              label="Assessment" onChange={this.handleChange}
              name="assessment" value={soap.assessment}
              validators={[ONLY_BLANK_SPACES]}
              errorMessages={[Constants.ERROR_MESSAGES.blank_spaces_not_allowed]}
            />
          </Grid>
          <Grid item xs={12}>
            <TextValidator
              fullWidth
              multiline
              rows="2"
              variant="outlined"
              rowsMax="4"
              label="Plan" onChange={this.handleChange}
              name="plan" value={soap.plan}
              validators={[ONLY_BLANK_SPACES]}
              errorMessages={[Constants.ERROR_MESSAGES.blank_spaces_not_allowed]}
            />
          </Grid>

          {
            this.state.showError &&
            <Grid item xs={12} className={'error-at-least-one'}>Add at least 1 note</Grid>

          }
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary"
                    className="invite-add-user-btn">{"Save"}</Button>
          </Grid>
        </Grid>
      </ValidatorForm>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loggedInUser: state.auth.loggedInUser,
  }
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateConsultNote: data => {
      return dispatch(updateConsultNote(data)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },

    getConsultNotes: consultId => {
      return dispatch(getConsultNotes(consultId)).then(res => {
        return Promise.resolve(res);
      }, error => {
        return Promise.reject(error);
      });
    },
    showSnackbar: (message, variant) => {
      dispatch(showSnackbar(message, variant));
    }
  }
};

SoapForm = connect(
  mapStateToProps,
  mapDispatchToProps
)(SoapForm);

export default SoapForm;
